@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;600;800&display=swap);
.App {
  width: 100vw;
  height: 100vh;
  background: #99aab5;
  color: #212121;
  font-family: 'Poppins', sans-serif;
  display: grid;
  place-items: center;
}
body {
  margin: 0%;
  padding: 0%;
}

.joinChatContainer {
  padding: 20px;
  border: 1px #99aab5 solid;
  border-radius: 0.5rem;
  background-color: #5865f2;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.joinChatContainer h3 {
  font-size: 1.75rem;
  margin-bottom: 0.5rem;
  color: #fff;
}
.joinChatContainer input {
  width: 210px;
  height: 30px;
  margin: 7px;
  border: 2px solid #99aab5;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  font-family: inherit;
}
select {
  width: 225px;
  height: 40px;
  margin: 7px;
  border: 2px solid #99aab5;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  font-family: inherit;
}
.joinChatContainer button {
  width: 225px;
  height: 50px;
  margin: 7px;
  margin-bottom: 20px;
  margin-top: 10px;
  border: none;
  border-radius: 5px;
  padding: 5px;
  font-size: 16px;
  background: #36393f;
  color: #fff;
  cursor: pointer;
  font-family: inherit;
}

.joinChatContainer button:hover {
  background: #23272a;
}

.chat-window {
  width: 300px;
  height: 420px;
}

.chat-window p {
  margin: 0;
}

.online {
  height: 15px;
  width: 15px;
  border-radius: 100%;
  background-color: #43a047;
  position: absolute;
  top: 15px;
  left: 10px;
}

.chat-window .chat-header {
  display: flex;
  height: 45px;
  border-radius: 6px;
  background: #263238;
  position: relative;
  cursor: pointer;
}
.chat-window .chat-header p {
  display: block;
  padding: 0 1em 0 2.2em;
  color: #fff;
  font-weight: 700;
  line-height: 45px;
}

.chat-window .chat-body {
  height: calc(450px - (45px + 70px));
  border: 1px solid #263238;
  background: #fff;

  position: relative;
}
.chat-window .chat-body .message-container {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.chat-window .chat-body .message-container::-webkit-scrollbar {
  display: none;
}
.chat-window .chat-body .message {
  height: auto;
  padding: 10px;
  display: flex;
}

.chat-window .chat-body .message .message-content {
  width: auto;
  height: auto;
  min-height: 40px;
  max-width: 120px;
  background-color: #43a047;
  border-radius: 5px;
  color: white;
  display: flex;
  align-items: center;
  margin-right: 5px;
  margin-left: 5px;
  padding-right: 5px;
  padding-left: 5px;
  overflow-wrap: break-word;
  word-break: break-word;
}

#you {
  justify-content: flex-start;
}

#you .message-content {
  justify-content: flex-start;
}

#you .message-meta {
  justify-content: flex-start;
  margin-left: 5px;
}

#other {
  justify-content: flex-end;
}

#other .message-content {
  justify-content: flex-end;
  background-color: cornflowerblue;
}

#other .message-meta {
  justify-content: flex-end;
  margin-right: 5px;
}

.message-meta #author {
  margin-left: 10px;
  font-weight: bold;
}

.chat-window .chat-body .message .message-meta {
  display: flex;
  font-size: 12px;
}

.chat-window .chat-footer {
  height: 40px;
  border: 1px solid #263238;
  border-top: none;
  display: flex;
}

.chat-window .chat-footer input {
  height: 100%;
  flex: 85% 1;
  border: 0;
  padding: 0 0.7em;
  font-size: 1em;
  border-right: 1px dotted #607d8b;

  outline: none;
  font-family: 'Open Sans', sans-serif;
}

.chat-window .chat-footer button {
  border: 0;
  display: grid;
  place-items: center;
  cursor: pointer;
  flex: 15% 1;
  height: 100%;
  background: transparent;
  outline: none;
  font-size: 25px;
  color: #202225;
}

.chat-window .chat-footer button:hover {
  color: #43a047;
}
.hide {
  opacity: 0 !important;
}

